.manage-page {
  font-family: 'PingFangSC-Regular', 'Microsoft YaHei', sans-serif;
}

::-webkit-scrollbar {
  width: 5px; /* 纵向滚动条*/
  height: 5px; /* 横向滚动条 */
  background-color: #fff;
}

/*定义滚动条轨道 内阴影*/
::-webkit-scrollbar-track {
  --webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  background-color: #fff;
}

/*定义滑块 内阴影*/
::-webkit-scrollbar-thumb {
  --webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0);
  background-color: #d5d5d5;
}

/* 表格形状固定 */
.ant-table table {
  table-layout: fixed;
  overflow: 'hidden';
}
